
import s from './Post.module.css'
import img3 from './img3.jpeg'


const Post = (props) => {
  

    return       (  
       <div className={s.item}>
   <img  src={img3} />
           <span className={s.text}> {props.name} </span>
 <span className={s.like}>Like</span>
     </div>
    )    
}
 




export default Post;