import s from './ProfileInfo.module.css'

import Preloader from "../../common/preloader/Preloader";
import React from "react";
import img1 from '../../../img/profile.png'
import ProfileStatus from "./ProfileStatus";


const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader/>
    }


    return (
        <div>

            <div className={s.img31}><img
                src='https://www.purina.ru/sites/default/files/2021-02/kot-ili-koshka-header%20%281%29.jpg'
                className={s.img3}/></div>
            <div className={s.block}>
                <div><img src={props.profile.photos.large != null ? props.profile.photos.large : img1}
                          className={s.img2}/>
                    <ProfileStatus
                        status={props.status}
                        updateStatus={props.updateStatus}
                    />
                    {/*  Прокидывать выше */}

                </div>
                <div className={s.about}>
                    <div>Описание:</div>
                    <div>{props.profile.fullName}</div>
                    <div>{props.profile.aboutMe}</div>
                    <div>{props.profile.contacts.vk}</div>
                    <div>{props.profile.contacts.vk}</div>
                    <div>{props.profile.contacts.instagram} </div>
                    <div>{props.profile.contacts.website} </div>
                </div>
            </div>

        </div>
    )
}


export default ProfileInfo;